import { signOut } from "firebase/auth";
import { call, put } from "redux-saga/effects";
import { auth } from "../api/firebaseConnection";
import { userProfileSlice } from "../store/userProfile";
import { createCommand } from "../utilities/createCommand";
import { requestPermission } from "../utilities/messaging";

export const signOutCommand = createCommand<void>(
  'signOut',
  function* () {
    yield call(signOut, auth);
    yield put(userProfileSlice.actions.resetUserProfileState());
  },
);

export const checkNotificationsPermissionCommand = createCommand<void>(
  'checkNotificationsPermission',
  function* () {
    const token = yield call(requestPermission);
    yield put(userProfileSlice.actions.setNotificationsToken(token));
  },
);
