import { selectIsSubjectShowMore, selectIsTeacherShowMore } from "./novigation";
import { TeacherSelector } from "./types";

export const seeMoreButtonHeight = 42;
export const photoHeight = 60;
export const avatarHeight = photoHeight - 16;
export const descriptionDefaultHeight = 120;
export const showMoreDescriptionHeight = descriptionDefaultHeight + 180;

export const profileDefaultHeight = photoHeight + descriptionDefaultHeight + 50 + 40;
export const profileShowMoreHeight = photoHeight + showMoreDescriptionHeight + 50 + 60;
export const subjectDescriptionContenerClassName = 'subject-description-wrapper';

export const selectSubjectDescriptionHeight: TeacherSelector<number> = (state) => {
  const showMore = selectIsSubjectShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};

export const selectDescriptionHeightWithPosibleButton: TeacherSelector<number> = (state) => {
  const descriptionWrapper = document.querySelector(`.${subjectDescriptionContenerClassName}`);
  const descriptionHeightWithPosibleButton = descriptionWrapper ? descriptionWrapper.clientHeight : 157;
  console.log('descriptionHeightWithPosibleButton', descriptionHeightWithPosibleButton); 
  console.log('descriptionWrapper.clientHeight', descriptionWrapper?.clientHeight); 
  return descriptionHeightWithPosibleButton;
}

export const selectTeacherDescriptionHeight: TeacherSelector<number> = (state) => {
  const showMore = selectIsTeacherShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};
