import React from 'react';
import { useDispatch } from 'react-redux';
import { checkNotificationsPermissionCommand } from '../../../common/commands';
import { onMessageListener } from '../../../common/utilities/messaging';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Box } from '@mui/material';
import { IconButton } from '../../../common/component/IconButton';
import { ButtonType } from '../../../common/component/IconNavButton';


export const NotificationButton: React.FC = () => {
  const dispatch = useDispatch();
  onMessageListener().then(({notification}) => {
    toast.success(JSON.stringify(notification));
  });
  return (
    <Box>
      <IconButton
        type={ButtonType.notifications}
        onClick={() => dispatch(checkNotificationsPermissionCommand.action())}
      />
      <ToastContainer 
        limit={5}
        position="top-center"
        autoClose={3000}
        hideProgressBar={true}
        newestOnTop={false}
        closeOnClick
        theme={'colored'}
      />
    </Box>
  );
}
