import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { FirebaseUserInfo } from "../types/utility";

const userProfileInitialState: FirebaseUserInfo = {
  id: '',
  displayName: '',
  email: '',
  photoURL: '',
  providerId: '',
  phoneNumber: '',
  notificationsToken: '',
}

export const userProfileSlice = createSlice({
  name: 'teacherModal',
  initialState: userProfileInitialState,
  reducers: {
    setUserProfileState: (state, { payload: stateDiff }: PayloadAction<Partial<FirebaseUserInfo>>) => ({
      ...state,
      ...stateDiff,
    }),
    resetUserProfileState: () => ({...userProfileInitialState}),
    setNotificationsToken: (state, { payload: token }: PayloadAction<string>): FirebaseUserInfo => ({
      ...state,
      notificationsToken: token,
    }),
  },
});
